@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+TC:wght@300;400;500;700;900&display=swap");
.font-noto {
  font-family: 'Noto Sans TC', sans-serif;
}

.w-6s {
  width: 6rem;
}

.h-15s {
  height: 238px;
}

.h-26s {
  height: 420px;
}

.h-40s {
  height: 628px;
}

@media (min-width: 768px) {
  .h-md-15s {
    height: 252px;
  }
}

@media (min-width: 992px) {
  .h-lg-26s {
    height: 420px;
  }
}

.square-3s {
  height: 48px;
  width: 48px;
}

.square-4s {
  height: 60px;
  width: 60px;
}

.square-5s {
  height: 68px;
  width: 68px;
}

.square-6s {
  height: 94px;
  width: 94px;
}

@media (min-width: 576px) {
  .position-sm-absolute {
    position: absolute;
  }
}

@media (min-width: 992px) {
  .position-lg-absolute {
    position: absolute;
  }
  .translate-lg-middle {
    transform: translate(-50%, -50%) !important;
  }
}

.hover-secondary-1:hover {
  color: #a79697 !important;
}

.hover-dark-1:hover {
  color: #a3a3a3 !important;
}

.hover-white-1:hover {
  color: #cccccc !important;
}

.hover-extend-1 {
  transform: scale(1, 1);
  transition: all 0.8s ease-out;
}

.hover-extend-1:hover {
  transform: scale(1.2, 1.2);
}

.img-cover {
  object-fit: cover;
  object-position: center;
}

.rounded-1s {
  border-radius: 1rem;
}

.rounded-top-1s {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

@media (min-width: 768px) {
  .rounded-md-start {
    border-top-left-radius: 1rem;
    border-top-right-radius: 0;
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 0;
  }
}

@media (min-width: 992px) {
  .rounded-md-top {
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
}

body {
  font-family: 'Noto Sans TC', sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

ul {
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

a {
  text-decoration: none;
}

.banner {
  min-height: 560px;
  background: url(../images/banner-1.jpg);
  background-size: cover;
  background-position: 50% 100%;
}

@media (min-width: 768px) {
  .banner {
    min-height: 582px;
  }
}

@media (min-width: 768px) {
  .ms-off {
    margin-left: -60px;
  }
}

@media (min-width: 992px) {
  .ms-off {
    margin-left: -95px;
  }
}

.community-list {
  z-index: 1;
}

.block-py {
  padding-top: 48px;
  padding-bottom: 48px;
}

@media (min-width: 768px) {
  .block-py {
    padding-top: 56px;
    padding-bottom: 56px;
  }
}

@media (min-width: 992px) {
  .block-py {
    padding-top: 64px;
    padding-bottom: 64px;
  }
}

.index .relax .img-primary {
  height: 210px;
}

@media (min-width: 768px) {
  .index .relax .img-primary {
    height: 266px;
  }
}

@media (min-width: 992px) {
  .index .relax .img-primary {
    height: 444px;
  }
}

.index .relax .img-secondary {
  height: 210px;
}

@media (min-width: 768px) {
  .index .relax .img-secondary {
    height: 117px;
  }
}

@media (min-width: 992px) {
  .index .relax .img-secondary {
    height: 207px;
  }
}

.index .featured .container-course {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto;
}

@media (min-width: 576px) {
  .index .featured .container-course {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .index .featured .container-course {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .index .featured .container-course {
    max-width: 100%;
  }
}

.index .featured .swiper-wrapper {
  flex-wrap: wrap !important;
}

@media (min-width: 992px) {
  .index .featured .swiper-wrapper {
    flex-wrap: nowrap !important;
  }
}

.index .featured .swiper-slide {
  width: 48%;
  margin: 0.5rem 1%;
}

@media (min-width: 992px) {
  .index .featured .swiper-slide {
    width: 100%;
  }
}

.index .featured .course-main {
  width: 100%;
}

@media (min-width: 576px) {
  .index .featured .course-main {
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    transform: translateY(-50%);
  }
}

.index .featured .swiper-course img {
  height: 165px;
}

@media (min-width: 576px) {
  .index .featured .swiper-course img {
    height: 150px;
    width: 150px;
  }
}

@media (min-width: 992px) {
  .index .featured .swiper-course img {
    height: 445px;
    width: 445px;
  }
}

.index .customer-feedback .swiper-slide {
  display: flex;
  justify-content: center;
}
